@import '../../sass/base.sass';

#root {
  height: 100%; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%; }

.grid-container {
  display: grid;
  grid-template-columns: repeat(5, minmax(80px, 120px));
  grid-template-rows: repeat(4, minmax(80px, 120px));
  grid-gap: 10px; }

.fruit__figure {
  margin: 0;
  padding: 5px; }

.fruit__img {
  display: block;
  width: 100%;
  height: 100%;
  transition: opacity 300ms;
  opacity: 0; }

.tile {
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;
  background: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 100%;
  height: 100%;
  transition: transform 300ms cubic-bezier(0.175, 0.885, 0.320, 1.275),
  box-shadow 300ms cubic-bezier(0.175, 0.885, 0.320, 1.275),
  border 300ms {}
  &:not(.choosen):hover {
    transform: translateZ(50px);
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.35); }
  &.choosen {
    border: 5px solid #08ff81; }
  &.completed, &.choosen {
    .fruit__img {
      opacity: 1; } } }

@media (max-width: 1120px) {
  .grid-container {
    grid-template-columns: repeat(5, minmax(40px, 80px));
    grid-template-rows: repeat(4, minmax(40px, 80px)); } }

@media (max-width: 460px) {
  .grid-container {
    grid-template-columns: repeat(5, minmax(20px, 60px));
    grid-template-rows: repeat(4, minmax(20px, 60px)); } }
